<template>
  <div class="gantt-elastic-box">
    <GanttElastic
      :props-tasks="tasks"
      :props-options="options"
      @addTask="addTask"
    >
    </GanttElastic>
    <el-dialog title="" :visible.sync="dialogVisible" width="30%">
      <FormBox
        :data-form="dataForm"
        :rules="rules"
        @addFormData="addFormData"
      />
    </el-dialog>
  </div>
</template>

<style></style>

<script>
import GanttElastic from '@/components/GanttElastic'
import dayjs from 'dayjs'
function getDate(hours) {
  const currentDate = new Date()
  const currentYear = currentDate.getFullYear()
  const currentMonth = currentDate.getMonth()
  const currentDay = currentDate.getDate()
  const timeStamp = new Date(
    currentYear,
    currentMonth,
    currentDay,
    0,
    0,
    0
  ).getTime()
  return new Date(timeStamp + hours * 60 * 60 * 1000).getTime()
}
export default {
  name: 'Gantt',
  components: {
    GanttElastic
  },
  data() {
    return {
      dialogVisible: false,
      // 
      tasks: [
        {
          id: 1,
          label: '1', // 
          user:
            '<a href="https://www.google.com/search?q=" target="_blank" style="color:#0077c0;"></a>',
          start: getDate(-24 * 5), // 
          duration: 15 * 24 * 60 * 60 * 1000, // 
          percent: 85, // 
          type: 'project' // projectmilestonetask
          //collapsed: true,
        },
        {
          id: 2,
          label: '2',
          user:
            '<a href="https://www.google.com/search?q=" target="_blank" style="color:#0077c0;"></a>',
          parentId: 1, // id
          start: getDate(-24 * 4),
          duration: 4 * 24 * 60 * 60 * 1000,
          percent: 50,
          type: 'milestone',
          collapsed: true,
          // 
          style: {
            base: {
              fill: '#1EBC61', // 
              stroke: '#0EAC51' // 
            },
            text: {
              color: '#108cee'
            }
          }
        },
        {
          id: 3,
          label: '3.',
          user:
            '<a href="https://www.google.com/search?q=" target="_blank" style="color:#0077c0;"></a>',
          parentId: 2,
          start: getDate(-24 * 3),
          duration: 2 * 24 * 60 * 60 * 1000,
          percent: 100,
          type: 'task'
        },
        {
          id: 4,
          label: '4',
          user:
            '<a href="https://www.google.com/search?q=" target="_blank" style="color:#0077c0;"></a>',
          start: getDate(-24 * 2),
          duration: 2 * 24 * 60 * 60 * 1000,
          percent: 50,
          type: 'task',
          dependentOn: [3]
        },
        {
          id: 5,
          label: '5',
          user:
            '<a href="https://www.google.com/search?q=" target="_blank" style="color:#0077c0;"></a>',
          parentId: 4,
          start: getDate(0),
          duration: 2 * 24 * 60 * 60 * 1000,
          percent: 10,
          type: 'milestone',
          style: {
            base: {
              fill: '#0287D0',
              stroke: '#0077C0'
            }
          }
        },
        {
          id: 6,
          label: '6',
          user:
            '<a href="https://www.google.com/search?q=" target="_blank" style="color:#0077c0;"></a>',
          parentId: 5,
          start: getDate(24),
          duration: 1 * 24 * 60 * 60 * 1000,
          percent: 50,
          type: 'task',
          collapsed: true,
          style: {
            base: {
              fill: '#8E44AD',
              stroke: '#7E349D'
            }
          }
        },
        {
          id: 7,
          label: '7',
          user:
            '<a href="https://www.google.com/search?q=" target="_blank" style="color:#0077c0;"></a>',
          parentId: 2,
          dependentOn: [6],
          start: getDate(24 * 2),
          duration: 4 * 60 * 60 * 1000,
          percent: 20,
          type: 'task',
          collapsed: true
        },
        {
          id: 8,
          label: '8',
          user:
            '<a href="https://www.google.com/search?q=" target="_blank" style="color:#0077c0;"></a>',
          parentId: 7,
          dependentOn: [7],
          start: getDate(24 * 3),
          duration: 1 * 24 * 60 * 60 * 1000,
          percent: 0,
          type: 'task'
        },
        {
          id: 9,
          label: '9',
          user:
            '<a href="https://www.google.com/search?q=" target="_blank" style="color:#0077c0;"></a>',
          parentId: 8,
          dependentOn: [8, 7],
          start: getDate(24 * 4),
          duration: 4 * 60 * 60 * 1000,
          percent: 20,
          type: 'task',
          style: {
            base: {
              fill: '#8E44AD',
              stroke: '#7E349D'
            }
          }
        },
        {
          id: 10,
          label: '10',
          user:
            '<a href="https://www.google.com/search?q=" target="_blank" style="color:#0077c0;"></a>',
          start: getDate(24 * 5),
          duration: 24 * 60 * 60 * 1000,
          percent: 0,
          type: 'task'
        },
        {
          id: 11,
          label: '11',
          user:
            '<a href="https://www.google.com/search?q=" target="_blank" style="color:#0077c0;"></a>',
          start: getDate(24 * 6),
          duration: 24 * 60 * 60 * 1000,
          percent: 0,
          type: 'task'
        },
        {
          id: 12,
          label: '12',
          user:
            '<a href="https://www.google.com/search?q=" target="_blank" style="color:#0077c0;"></a>',
          start: getDate(24 * 7),
          duration: 24 * 60 * 60 * 1000,
          percent: 0,
          type: 'task',
          parentId: 11
        },
        {
          id: 13,
          label: '13',
          user:
            '<a href="https://www.google.com/search?q=" target="_blank" style="color:#0077c0;"></a>',
          start: getDate(24 * 8),
          duration: 24 * 60 * 60 * 1000,
          percent: 0,
          type: 'task'
        },
        {
          id: 14,
          label: '14',
          user:
            '<a href="https://www.google.com/search?q=" target="_blank" style="color:#0077c0;"></a>',
          start: getDate(24 * 9),
          duration: 24 * 60 * 60 * 1000,
          percent: 0,
          type: 'task'
        },
        {
          id: 15,
          label: '15',
          user:
            '<a href="https://www.google.com/search?q=" target="_blank" style="color:#0077c0;"></a>',
          start: getDate(24 * 16),
          duration: 24 * 60 * 60 * 1000,
          percent: 0,
          type: 'task'
        }
      ],
      // 
      options: {
        taskMapping: {
          progress: 'percent' // -
        },
        maxRows: 100, // 
        maxHeight: 500, // 
        defHeader: false, // has slot header
        title: {
          label: '',
          html: false
        },
        row: {
          height: 24 // 
        },
        times: {
          timeZoom: 16, // 
          timeScale: 60 * 1000 // 
        },
        // scope: {
        //   befoce: 10,
        //   after: 10
        // },
        // 
        calendar: {
          hour: {
            display: true
          }
        },
        // 
        chart: {
          progress: {
            bar: false
          },
          // icon
          expander: {
            type: 'chart',
            display: true, // 
            offset: 4, // 
            size: 18 // 
          },
          text: {
            display: true, // 
            offset: 4 // icon
          }
        },
        // 
        taskList: {
          expander: {
            straight: false
          },
          columns: [
            {
              id: 1,
              label: 'ID',
              value: 'id', // tasks
              width: 40
            },
            {
              id: 2,
              label: '',
              value: 'label',
              width: 200,
              expander: true, // 
              html: true,
              events: {
                click({ data, column }) {
                  alert('description clicked!\n' + data.label)
                }
              }
            },
            {
              id: 3,
              label: '',
              value: 'user',
              width: 130,
              html: true
            },
            {
              id: 3,
              label: '',
              value: (task) => dayjs(task.start).format('YYYY-MM-DD'),
              width: 78
            },
            {
              id: 4,
              label: '',
              value: 'type',
              width: 68
            },
            {
              id: 5,
              label: '%',
              value: 'progress',
              width: 35,
              style: {
                'task-list-header-label': {
                  'text-align': 'center',
                  width: '100%'
                },
                'task-list-item-value-container': {
                  'text-align': 'center',
                  width: '100%'
                }
              }
            }
          ]
        },
        // 
        locale: {
          name: 'en',
          Now: '',
          'X-Scale': '',
          'Y-Scale': '',
          'Task list width': '',
          'Before/After': '',
          'Display task list': '',
          weekdays: ['', '', '', '', '', '', ''],
          months: [
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            '',
            ''
          ]
        },
        calendar: {
          workingDays: [1, 2, 3, 4, 5, 6], // 
          gap: 0, // 
          strokeWidth: 5, //
          hour: {
            display: true // 
          }
        }
      },
      lastId: 16,
      dataForm: {
        xtype: '',
        labelWidth: '100px',
        status: 'createForm',
        label: [
          {
            xtype: 'text',
            name: 'name',
            text: ''
          },
          {
            xtype: 'text',
            name: 'user',
            text: ''
          },
          {
            xtype: 'date',
            name: 'start',
            text: ''
          },
          {
            xtype: 'select',
            name: 'type',
            text: '',
            selectList: [
              {
                label: 'project',
                value: 'project'
              },
              {
                label: 'milestone',
                value: 'milestone'
              },
              {
                label: 'task',
                value: 'task'
              }
            ]
          },
          {
            xtype: 'unit',
            name: 'percent',
            text: '',
            unit: '%'
          }
        ]
      },
      rules: {
        name: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 3,
            max: 20,
            message: ' 3  20 ',
            trigger: 'blur'
          }
        ],
        user: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        start: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  methods: {
    addTask() {
      console.log('aaa')
      this.dialogVisible = true
    },
    addFormData(data) {
      console.log(data)
      this.tasks.push({
        id: this.lastId++,
        label:
          '<a href="https://images.pexels.com/photos/423364/pexels-photo-423364.jpeg?auto=compress&cs=tinysrgb&h=650&w=940" target="_blank" style="color:#0077c0;">' +
          data.name +
          '</a>',
        user:
          '<a href="https://images.pexels.com/photos/423364/pexels-photo-423364.jpeg?auto=compress&cs=tinysrgb&h=650&w=940" target="_blank" style="color:#0077c0;">' +
          data.user +
          '</a>',
        start: data.date,
        duration: 1 * 24 * 60 * 60 * 1000,
        percent: data.percent ? data.percent : 10,
        type: data.type ? data.type : 'project'
      })

      this.dialogVisible = false
    }
  }
}
</script>
<style lang="scss" scoped>
.gantt-elastic-box {
  width: 100%;
  height: 100%;
}
</style>



