<template>
  <div class="">
    <TableHeader :title="title" :btn-list="btnList" />
    <TableBox
      :data-table="table"
      :filter-data="filterData"
      :is-selection-show="true"
    />
  </div>
</template>

<script>
import TableHeader from '@/components/TableBox/components/TableHeader'
export default {
  name: 'Table',
  components: {
    TableHeader
  },
  data() {
    return {
      title: '',
      btnList: [
        {
          name: 'staticFileUpload',
          title: '',
          show: false,
          slotName: 'StaticFileUpload'
        },
        {
          name: 'addApi',
          title: '',
          show: false,
          slotName: 'EditApiForm'
        }
      ],

      table: {
        th: [
          {
            xtype: 'text',
            name: 'dataSourceName',
            text: ''
          },
          {
            xtype: 'text',
            name: 'description',
            text: ''
          },
          {
            xtype: 'text',
            name: 'createUserId',
            text: '',
            width: 150
          },
          {
            xtype: 'switch',
            name: 'enable',
            text: '',
            tip: {
              icon: 'icon-yiwen',
              content:
                '',
              placement: 'top',
              trigger: 'click'
            },
            width: 120
          },
          {
            xtype: 'valided',
            name: 'valided',
            text: '',
            width: 80
          },
          {
            xtype: 'dataSourceType',
            name: 'dataSourceType',
            text: '',
            width: 100
          }
        ],
        handle: [
          // type'primary''success''warning''info''danger'
          { name: '', type: 'primary', href: '#' },
          {
            name: '',
            type: 'success',
            href: '#/data-preview'
          },
          { name: '', type: 'warning', href: '#' },
          { name: '', type: 'info', href: '#' },
          { name: '', type: 'danger', href: '#' }
        ],
        tr: []
      },
      filterData: [
        {
          dataSourceName: 'aaa',
          description: 'bbb',
          createUserId: 'ccc',
          enable: 'ddd'
        }
      ]
    }
  },
  methods: {
    selectList(params) {
      console.log('params', params)
    }
  }
}
</script>

<style lang="scss" scoped></style>



