import Vue from 'vue'
Vue.directive('drag-icon', {
  // 
  inserted: function(el) {
    const oIcon = el // 
    oIcon.style.cursor = 'move'
    const oDiv = oIcon.parentNode // div
    oDiv.style.position = 'fixed'
    oDiv.style.zIndex = '1'
    oIcon.onmousedown = (e) => {
      // 
      const disX = e.clientX - oDiv.offsetLeft
      const disY = e.clientY - oDiv.offsetTop
      document.onmousemove = (e) => {
        // 
        const left = e.clientX - disX
        const top = e.clientY - disY

        const oDivW = oDiv.offsetWidth // div
        const oDivH = oDiv.clientHeight // div
        const docW = document.body.clientWidth // 
        const docH = document.body.clientHeight // 
        const maxW = docW - oDivW // 
        const maxH = docH - oDivH // 

        // 
        oDiv.style.left = left + 'px'
        oDiv.style.top = top + 'px'
        if (left < 0) {
          oDiv.style.left = 0 + 'px'
        }
        if (top < 0) {
          oDiv.style.top = 0 + 'px'
        }
        if (left > maxW) {
          oDiv.style.left = maxW + 'px'
        }
        if (top > maxH) {
          oDiv.style.top = maxH + 'px'
        }
      }
      document.onmouseup = (e) => {
        document.onmousemove = null
        document.onmouseup = null
      }
    }
  }
})



