<template>
  <div class="form-box">
    <h5></h5>
    <form action="">
      <Input class="form-item" :form="form.total" />
      <Input class="form-item" :form="form.comeTime" />
      <Input class="form-item" :form="form.offTime"  :timeCost="timeCost"/>
      <Input class="form-item" :form="form.purchaseNum"/>
      <Input class="form-item" :form="form.afterTheStandard" />
      <Input class="form-item" :form="form.defaultRate" />
      <InputSelect class="form-item" :form="form.qualityStandard" />
      <InputSelect class="form-item" :form="form.typeOfPayment" />
      <InputSelect class="form-item" :form="form.typeOfPayment1" />
    </form>
    <h5 style="margin-top: 50px"></h5>
    <div class="contract-main">
      <ContractMain :main="main.firstParty" />
      <ContractMain :main="main.secondParty" />
      <ContractMain :main="main.thirdParty" />
    </div>
    <h5 style="margin-top: 30px"></h5>
  </div>
</template>

<script>
import Input from "./InputText";
import InputSelect from "./InputSelect";
import ContractMain from "./ContractMain";
export default {
  name: "Form",
  components:{
    Input,
    InputSelect,
    ContractMain
  },
  data() {
    return {
      comeTimeValue: '2022-01-18',
      offTimeValue: '2025-01-18',
      form: {
        total: {
          title:'',
          value:'290,000',
          isLockInput:true,
          isUnlockInput:false,
        },
        comeTime:{
          title:'',
          value:'2022-01-18',
          isLockInput:false,
          isUnlockInput:true,
        },
        offTime:{
          title:'',
          value:'2029-01-18',
          isLockInput:false,
          isUnlockInput:true,
        },
        purchaseNum: {
          title:'',
          value:'290,000',
          isLockInput:true,
          isUnlockInput:false,
        },
        afterTheStandard: {
          title:'',
          value:'',
          isLockInput:true,
          isUnlockInput:false,
        },
        defaultRate: {
          title:'',
          value:'25%',
          isLockInput:true,
          isUnlockInput:false,
          alarm:'',
          alarmText:''
        },
        qualityStandard: {
          title:'',
          value:[
            '1',
            '2',
            '3',
          ],
        },
        typeOfPayment:{
          title:'',
          value:[
            '',
            '',
            '',
          ],
        },
        typeOfPayment1:{
          title:'1',
          value:[
            '1',
            '1',
            '1',
          ],
        },
      },
      main:{
        firstParty:{
          title:'',
          value:[
            '',
          ],
        },
        secondParty:{
          title:'',
          value:[
            '',
            '',
          ],
        },
        thirdParty:{
          title:'',
          value:[
            '',
          ],
        }
      }
    }
  },
  computed:{
    timeCost(){
      let comeTime = this.form.comeTime.value,
          offTime = this.form.offTime.value
      //
      let date3 = new Date(offTime).getTime() - new Date(comeTime).getTime();
      //
      let days = Math.floor(date3/(24*3600*1000))
      //
      let years = Math.floor(days/365)
      return years + ''
    }
  },
}
</script>

<style scoped lang="scss">
.form-box{
  margin-left: 40px;
  padding: 50px 0 50px 50px;
  box-sizing: border-box;
  border-radius: 8px;
  background: #fff;
  overflow: hidden;
  h5{
    padding: 3px 0;
    text-align: left;
    text-indent: 1em;
    border-radius: 5px;
    color: #53A4FB;
    font-weight: 700;
    background: linear-gradient(90deg, #F1F3F4, #fff, #fff);
  }
  form,.contract-main{
    display:flex;
    flex-wrap: wrap;
    width: 100%;
    margin-top: 10px;
    &>div{
      width: calc(100% / 3);
      margin:30px 0;
      padding-right: 8%;
      box-sizing: border-box;
    }
  }
  .contract-main{
    width: 96%;
    margin-left: -30px;
    &>div{
      margin:0;
      padding-right: 0;
    }
  }
}
</style>



