<template>
  <div class="FormBox">
    <FormBox :data-form="form" :list-form="listForm" :rules="rules" />
  </div>
</template>

<script>
export default {
  name: 'Form',
  data() {
    return {
      form: {
        size: 'mini',
        xtype: '', // 
        labelWidth: '100px', // label
        status: 'updateForm', // updateForm createForm searchForm 
        // 
        label: [
          // 
          {
            xtype: 'table', // 
            name: 'table', // 
            text: '', // 
            // 
            table: {
              // 
              th: [
                {
                  text: '', // 
                  name: 'name', // tr
                  xtype: 'text' // 
                },
                {
                  text: '',
                  name: 'sex',
                  xtype: 'select',
                  selectList: [
                    {
                      label: '',
                      value: 'aaa'
                    },
                    {
                      label: '',
                      value: 'ddd'
                    }
                  ]
                }
              ],
              tr: [],
              // tr
              trItem: {
                name: '',
                sex: ''
              },
              // 
              rules: {
                name: [
                  {
                    required: true,
                    message: '',
                    trigger: 'blur'
                  }
                ]
              }
            }
          },
          // 
          {
            xtype: 'switch',
            name: 'switch',
            text: ''
          },
          // 
          {
            xtype: 'text',
            name: 'text',
            text: ''
          },
          // 
          {
            xtype: 'password',
            name: 'password',
            text: ''
          },
          // 
          {
            xtype: 'textarea',
            name: 'textarea',
            text: ''
          },
          // +
          {
            xtype: 'unit',
            name: 'unit',
            text: '+',
            unit: 'px',
            inputWidth: 150 // 
          },
          // 
          {
            xtype: 'select',
            name: 'select',
            text: '',
            selectList: [
              { value: '1', label: '1' },
              { value: '2', label: '2' }
            ]
          },
          // 
          {
            xtype: 'date',
            name: 'date',
            text: ''
          },
          // 
          {
            xtype: 'time',
            name: 'time',
            text: ''
          },
          // -
          {
            xtype: 'checked',
            name: 'checked',
            text: '-',
            checked: '-'
          },
          // --
          {
            xtype: 'checkbox',
            name: 'checkbox',
            text: '-',
            checkboxList: ['-1', '-2']
          },
          // 
          {
            xtype: 'radio',
            name: 'radio',
            text: '',
            radioList: [
              { value: '1', label: '1' },
              { value: '2', label: '2' }
            ]
          },
          // -
          {
            xtype: 'number',
            name: 'number',
            text: ''
          }
        ]
      },
      // -formlabelname
      listForm: {
        checkbox: [], // --
        number: 1 // -
      },
      // -formlabelname
      rules: {
        text: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 3,
            max: 20,
            message: ' 3  20 ',
            trigger: 'blur'
          }
        ],
        date: [
          {
            type: 'date',
            required: true,
            message: '',
            trigger: 'change'
          }
        ]
      }
    }
  },
  methods: {
    selectList(params) {
      console.log('params', params)
    }
  }
}
</script>

<style lang="scss">
.FormBox {
  & > div {
    height: 100vh;
    overflow: hidden;
  }
}
</style>



